"use client"

import { create } from "zustand"
import { persist } from "zustand/middleware"
import type { User } from "./types"
import { getCurrentUser, setCurrentUser as setStorageUser, getUsers } from "./storage"

interface AuthState {
  user: User | null
  isAuthenticated: boolean
  login: (email: string, password: string) => Promise<{ success: boolean; error?: string }>
  logout: () => void
  register: (data: {
    email: string
    password: string
    username: string
    fullName: string
  }) => Promise<{ success: boolean; error?: string }>
  switchUser: (userId: string) => void
  initialize: () => void
}

export const useAuth = create<AuthState>()(
  persist(
    (set) => ({
      user: null,
      isAuthenticated: false,

      initialize: () => {
        const currentUser = getCurrentUser()
        if (currentUser) {
          set({ user: currentUser, isAuthenticated: true })
        }
      },

      login: async (email: string, password: string) => {
        // Simulate API call
        await new Promise((resolve) => setTimeout(resolve, 500))

        const users = getUsers()
        const user = users.find((u) => u.email === email)

        if (!user) {
          return { success: false, error: "Invalid email or password" }
        }

        // In a real app, you'd verify the password hash
        // For demo purposes, any password works
        setStorageUser(user.id)
        set({ user, isAuthenticated: true })
        return { success: true }
      },

      logout: () => {
        if (typeof window !== "undefined") {
          localStorage.removeItem("pet_social_current_user")
        }
        set({ user: null, isAuthenticated: false })
      },

      register: async (data) => {
        // Simulate API call
        await new Promise((resolve) => setTimeout(resolve, 500))

        const users = getUsers()

        // Check if email or username already exists
        if (users.some((u) => u.email === data.email)) {
          return { success: false, error: "Email already exists" }
        }
        if (users.some((u) => u.username === data.username)) {
          return { success: false, error: "Username already taken" }
        }

        // Create new user
        const newUser: User = {
          id: String(Date.now()),
          email: data.email,
          username: data.username,
          fullName: data.fullName,
          joinedAt: new Date().toISOString().split("T")[0],
          followers: [],
          following: [],
        }

        users.push(newUser)
        if (typeof window !== "undefined") {
          localStorage.setItem("pet_social_users", JSON.stringify(users))
        }

        setStorageUser(newUser.id)
        set({ user: newUser, isAuthenticated: true })
        return { success: true }
      },

      switchUser: (userId: string) => {
        const users = getUsers()
        const user = users.find((u) => u.id === userId)
        if (user) {
          setStorageUser(userId)
          set({ user, isAuthenticated: true })
        }
      },
    }),
    {
      name: "pet-social-auth",
      partialize: (state) => ({ user: state.user, isAuthenticated: state.isAuthenticated }),
    },
  ),
)
